import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);//vue

//Vuex.Store 
const store = new Vuex.Store({
  state: {
    // 
    login: false,
    token: '',
    // 
    userinfo: null
  },
  mutations: {
    // 
    SET_LOGIN(state, type) {
      // 
      state.login = type
    },
    // 
    SET_USER_INFO(state, userinfo){
      state.userinfo = userinfo
      // ajax
      // Vue.prototype.$ajax.LOGOUT().then(res=>{
      //   console.log(res);
      // })
    }
  }
})
export default store